import React from 'react'
import { Link } from 'gatsby'
import './pagination.css'

const Pagination = ({ prev, next, total, isFirst, isLast }) => (
    isFirst === isLast ? '' : (
        <section className="pagination">
            {isFirst ? <em>{`1 of ${total}`}</em> : <em><Link to={prev}>prev</Link></em>}
            {!isFirst && !isLast ? <em>{`${next - 1} of ${total}`}</em> : <em></em>}
            {isLast ? <em>{`${total} of ${total}`}</em> : <em><Link to={next}>next</Link></em>}
        </section>
    )
)

export default Pagination