import React from 'react'
import './article.css'
import { Link } from 'gatsby'

const Article = ({ date, title, body, to }) => (
    <article>
        <header>
            <em>{date}</em>
            <h2><Link to={to ? to : '#'}>{title}</Link></h2>
        </header>
        <p
            dangerouslySetInnerHTML={{ __html: body }}
        />
    </article>
)

export default Article